import axios from "axios";
import store from "@/store";
import router from "@/router";

const request = axios.create({
  // baseURL: "http://127.0.0.1",
  baseURL: process.env.VUE_APP_BASE_API,
  timeout: 10000,
});

request.interceptors.request.use(
  (config) => {
    // 
    // 
    // token
    // 1. 
    const { dsuserInfo } = store.state.dsuser;
    // 2. token
    if (dsuserInfo.token) {
      // 3. token
      config.headers.Authorization = `Bearer ${dsuserInfo.token}`;
    }
    return config;
  },
  (err) => {
    return Promise.reject(err);
  }
);

// res => res.data  data
request.interceptors.response.use(
  (res) => {
    // console.log(res.data.message);
    if (res.data.message == "voidtoken") {
      store.commit("dsuser/setUser", {});
      // const fullPath = encodeURIComponent(router.currentRoute.value.fullPath);
      // encodeURIComponent uri
      router.push("/login");
    }
    return res.data;
  },
  (err) => {
    // 401 
    if (err.response && err.response.status === 401) {
      // 1. 
      // 2. 
      // 3. 
      store.commit("dsuser/setUser", {});
      // 
      // `/user?a=10` $route.path === /user  $route.fullPath === /user?a=10
      // jsrouter.currentRoute.value.fullPath router.currentRoute ref
      const fullPath = encodeURIComponent(router.currentRoute.value.fullPath);
      // encodeURIComponent uri
      router.push("/login?redirectUrl=" + fullPath);
    }
    return Promise.reject(err);
  }
);

export default request;
